
import {route, router, routeParam} from "../main/index";

@route('/')
class Example {
    @route('test1')
    public test1() {
        console.log('test1');
    }

    @route('test2')
    public test2() {
        console.log('test2');
    }

    @route('posts/:id')
    public post(@routeParam id:string) {
        console.log('post id=', id);
    }
}

let example = new Example();



document.getElementById('test1').onclick = (e) => {
    e.preventDefault();
    example.test1();
    return false;
};


router.init();


