import * as Route from "route-parser";

class MethodEntry {
    target:typeof Object;
    methodName:string;
    path:string;
}

type RouteParams = { [key:string]:string }
type RouteHandler = (params:RouteParams) => void;

export interface Router {
    init():void;
}

class RouterImpl implements Router {

    private methods:MethodEntry[] = [];

    private routes:Map<Route, RouteHandler> = new Map<Route, RouteHandler>();

    constructor() {
        console.log('RouterImpl');
    }

    public addRouteMethod(target:typeof Object, methodName:string, path:string) {
        this.methods.push({ target, methodName, path });
    }

    public addRouteObject(routeObject:any, path:string) {
        for (let entry of this.methods) {
            if (routeObject instanceof entry.target) {
                let fullPath = path + entry.path;

                let route = new Route(fullPath);
                this.routes.set(route, (params:RouteParams) => {
                    let handler = routeObject[entry.methodName] as Function;
                    handler.apply(routeObject, params);
                });
            }
        }
    }

    public init():void {
        console.log('RouterImpl::init');
    }
}

export function routeMethod(path:string):Function {
    return (target: typeof Object, propertyKey: string, descriptor: TypedPropertyDescriptor<any>) => {

        console.log("route decorator called with ", target, propertyKey, path, descriptor);

        routerImpl.addRouteMethod(target, propertyKey, path);

        return descriptor;
    };
}

export function routeClass(path?:string):Function {
    return (target:typeof Object) => {

        return class Wrapper extends target {
            constructor() {
                super();
                routerImpl.addRouteObject(this, path);
            }
        }

    };
}

export function routeParam(target:any, key:string, index:number) {
    console.log('routeParam called', target, key, index);
}

export function route(route:string) {
    return (...args : any[]) => {
        switch(args.length) {
            case 1:
                return routeClass(route).apply(this, args);
            case 2:
                throw new Error('decorator @route not supported on properties.');
            case 3:
                if(typeof args[2] === "number") {
                    throw new Error('decorator @route not supported on parameters.');
                }
                return routeMethod(route).apply(this, args);
            default:
                throw new Error('unsupported decorator signature');
        }
    }
}

const routerImpl:RouterImpl = new RouterImpl();

export const router:Router = routerImpl;


